<template>
		<view :class="theme">
			<view class="left">
				<image :src="url" mode="widthFix"></image>
				<text v-if="!is_fang" class="my-icons  btn" @tap="bo">&#xe7b0;</text>
				<text v-else class="my-icons btn dong" @tap="ting">&#xe502;</text>
			</view>
			<view class="center">
				<view class="info">
					<view class="title">
						{{name}}
					</view>
					<view class="desc">
						{{author}}
					</view>
				</view>
				<view class="time">
					{{time_str}}
				</view>
			</view>
		</view>
</template>

<script>
export default {
	name  : "lizhili-audio",
	props : {
		url : { type : String,  default : 'https://bjetxgzv.cdn.bspapp.com/VKCEYUGU-uni-app-doc/7fbf26a0-4f4a-11eb-b680-7980c8a877b8.png'},
		name :{ type : String,  default : '暂无标题'},
		author :{ type : String,  default : '佚名'},
		src:{ type : String,  default : null},
		theme:{ type : String,  default : 'black'},
	},
	data() {
		return {
			is_pause:false,
			is_fang:false,
			time:0,
			time_str:'00:00',
			intervalID:null,
			innerAudioContext:null
		};
	},
	watch: {
	    time(newName, oldName) {
			let fen=Math.floor(newName/60);
			let miao=newName%60;
			this.time_str=(fen >=10 ? fen : '0'+fen)+':'+(miao >=10 ? miao : '0'+miao)
	    }
	},
	created() {
		this.innerAudioContext=uni.createInnerAudioContext();
		this.innerAudioContext.onPlay(() => {
			if(!this.intervalID){
				this.intervalID=setInterval(()=>{
					if(!this.is_pause){
						this.time++
					}
				},1000)
			}
			this.is_pause=false
			this.is_fang=true
		});
		this.innerAudioContext.onCanplay((e) => {
			//console.log(this.innerAudioContext.duration);
		});
		this.innerAudioContext.onError((res) => {
			console.log('不能播放');
			console.log(res.errMsg);
			console.log(res.errCode);
			clearInterval(this.intervalID)
			this.is_fang=false
			this.is_pause=false
			this.time=0
			this.time_str='00:00'
		});
		this.innerAudioContext.onPause(() => {
			this.is_pause=true
			this.is_fang=false
		});
		this.innerAudioContext.onEnded(() => {
			console.log('播放结束');
			clearInterval(this.intervalID)
			this.is_fang=false
			this.is_pause=false
			this.time=0
			this.time_str='00:00'
		});
	},
	methods: {
		bo(){
			console.log('开始播放');
			this.innerAudioContext.src=this.src
			this.innerAudioContext.play()
		},
		ting(){
			console.log('暂停播放');
			this.innerAudioContext.pause()
		}
	}
};
</script>

<style lang="scss">
@font-face {
    font-family: 'iconfont';
    src: url('data:font/woff;charset=utf-8;base64,') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

.my-icons {
	font-family: 'iconfont' !important;
	font-size: 36px;
	font-style: normal;
	color: #FFFFFF;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
@keyframes changeright{
	0%{transform:rotate(0deg);}
	50%{transform:rotate(180deg);}
	100%{transform:rotate(360deg);}
}

.dong{
	animation:changeright 6s linear infinite;
}
.black{
	display: flex;
	overflow: hidden;
	border-radius: 10rpx;
	.left{
		width: 10%;
		background-color: #172239;
		padding: 15rpx;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		image{
			width: 100%;
			border-radius: 50%;
		}
		.btn{
			position: absolute;
			font-size: 40rpx;
		}
	}
	.center{
		justify-content: space-between;
		width: 90%;
		background-color: #525A6F;
		color: #FFFFFF;
		padding: 10rpx 20rpx;
		display: flex;
		flex-direction: row;
		.info{
			margin: 0 20rpx;
			.title{
				font-size: 28rpx;
			}
			.desc{
				margin-top: 15rpx;
				font-size: 24rpx;
			}
		}
		.time{
			display: flex;
			align-items: center;
			margin-left: 30rpx;
		}
	}
}
.white{
	display: flex;
	overflow: hidden;
	border-radius: 10rpx;
	.left{
		width: 10%;
		background-color: #DBDFE0;
		padding: 15rpx;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		image{
			width: 100%;
			border-radius: 50%;
		}
		.btn{
			position: absolute;
			font-size: 40rpx;
		}
	}
	.center{
		justify-content: space-between;
		width: 90%;
		background-color: #f1f1f1;
		color: #333333;
		padding: 10rpx 20rpx;
		display: flex;
		flex-direction: row;
		.info{
			margin: 0 20rpx;
			
			.title{
				font-size: 28rpx;
			}
			.desc{
				margin-top: 15rpx;
				font-size: 24rpx;
			}
		}
		.time{
			display: flex;
			align-items: center;
			margin-left: 30rpx;
		}
	}
}
</style>
